import React from 'react';
import './App.css';
import copy from 'copy-to-clipboard';



class Spawner extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      consultant: '',
      course: '',
      stuid: '',
      age: '',
      grade: '',
      basis: '零基础',
      with_parent: true,
      base_text: '',
      completion: '完成了课程的基本任务',
      comprehension: '好',
      answer: '',
      carefully: true,
      hands: '自己完成了任务',
      time: '',
      by_video: true,
      innovation: '',
      c_method: '可以语音进行交流',
      c_info: '并且交流表达较为清晰',
      can_ask: true,
      communication: '',
      network: false,
      computer: false,
      else_info: '',
      show_result: false,
      result: '',
    }
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
 
    this.setState({
      [name]: value
    });
  }

  spawn = ()=> {
    const showResult = this.state.show_result;
    /**如果当前没有显示结果,就进行生成 */
    if (!showResult) {
      let result = `# ${this.state.name} ${this.state.stuid} ${this.state.consultant} ${this.state.course}
【基础信息】${this.state.name+','} ${this.state.age+'岁, '}${this.state.grade+'年级, '}${this.state.with_parent?'家长陪同上课, ':''}${this.state.basis + ','}${this.state.network?'网络不稳定, ':''}${this.state.computer?'电脑基础操作较差, ':''} ${this.state.base_text+';'}
【完成度】${this.state.completion};
【理解能力】理解能力${this.state.comprehension}，${this.state.answer}${this.state.carefully?',上课能够认真听讲':''};
【动手能力】${this.state.hands}，${this.state.time?'完成任务花费了'+this.state.time+'分钟':''};
【创新能力】${this.state.by_video?'孩子只根据视频中的内容完成了任务':''}${this.state.innovation?','+this.state.innovation:''};
【交流表达能力】${this.state.c_method}, ${this.state.c_info}${this.state.can_ask?', 孩子可以主动向老师提出问题':''}${this.state.communication?', '+this.state.communication:''};`;

      if (this.state.else_info) {
        result+=`\n【其他】${this.state.else_info}`;
      }
      this.setState({
        result,
        show_result: !showResult
      })
    } else {
      this.setState({
        show_result: !showResult
      })
    }
  }

  render() {
    return (
      <div className='spawner'>
        {/** 基础信息 */}
        <div>
          <h3>基础信息</h3>
          <input placeholder='姓名' size='4' type='text' name='name' value={this.state.name} onChange={e=>this.handleInputChange(e)}/>
          <input placeholder='课程顾问' size='4' type='text' name='consultant' value={this.state.consultant} onChange={e=>this.handleInputChange(e)}/>
          <input placeholder='学生ID' style={{width: '6em'}} type='number' name='stuid' value={this.state.stuid} onChange={e=>this.handleInputChange(e)}/>
          <input placeholder='课程' size='4' type='text' name='course' value={this.state.course} onChange={e=>this.handleInputChange(e)}/>
          <br/>
          <input placeholder='年龄' style={{width: '3em'}} type='number' name='age' value={this.state.age} onChange={e=>this.handleInputChange(e)}/>
          <input placeholder='年级' style={{width: '3em'}} type='number' name='grade' value={this.state.grade} onChange={e=>this.handleInputChange(e)}/>
          <select name='basis' value={this.state.basis} onChange={e=>this.handleInputChange(e)}>
            <option value="零基础">零基础</option>
            <option value="接触过类似编程">接触过类似编程</option>
            <option value="学习过编程">学习过编程</option>
            <option value="接触过编程(代码)">接触过编程(代码)</option>
          </select>
          <input type='checkbox' name='with_parent' checked={this.state.with_parent} onChange={e=>this.handleInputChange(e)} id={'with_parent'+this.props.spawnerID}/>
          <label htmlFor={'with_parent'+this.props.spawnerID}>家长陪同</label>
          <br/>
          <input placeholder='补充信息: 对课程是否感兴趣' size='48' type='text' name='base_text' value={this.state.base_text} onChange={e=>this.handleInputChange(e)}/>
          <br/>
        </div>
        {/** 完成度 */}
        <div>
          <h3>完成度</h3>
          <select name='completion' value={this.state.completion} onChange={e=>this.handleInputChange(e)}>
            <option value="完成了课程的基本任务;">完成了课程的基本任务</option>
            <option value="完成了包括挑战的所有任务;">完成了包括挑战的所有任务</option>
            <option value="基本内容完成,但没有总结;">基本内容完成,但没有总结</option>
            <option value="课程任务没有完成;">课程任务没有完成</option>
            <option value="未完成任务一;">未完成任务一</option>
            <option value="未完成任务二;">未完成任务二</option>
            <option value="未完成任务三;">未完成任务三</option>
          </select>
        </div>
        {/** 理解能力 */}
        <div>
          <h3>理解能力</h3>
          <select name='comprehension' value={this.state.comprehension} onChange={e=>this.handleInputChange(e)}>
            <option value="好">好</option>
            <option value="较好">较好</option>
            <option value="中等">中等</option>
            <option value="略差">略差</option>
          </select>
          <select name='answer' value={this.state.answer} onChange={e=>this.handleInputChange(e)}>
            <option value="能准确快速的回答老师的问题">能准确快速的回答老师的问题</option>
            <option value="能准确的回答老师的问题">能准确的回答老师的问题</option>
            <option value="能大致回答出老师的问题">能大致回答出老师的问题</option>
            <option value="不能清楚的回答老师的问题">不能清楚的回答老师的问题</option>
          </select>
          <input type='checkbox' name='carefully' checked={this.state.carefully} onChange={e=>this.handleInputChange(e)} id={'carefully'+this.props.spawnerID}/>
          <label htmlFor={'carefully'+this.props.spawnerID}>认真听讲</label>
        </div>
        {/** 动手能力 */}
        <div>
          <h3>动手能力</h3>
          <select name='hands' value={this.state.hands} onChange={e=>this.handleInputChange(e)}>
            <option value="自己完成了任务">自己完成了任务</option>
            <option value="在父母提供部分帮助下自己完成了任务">在父母的部分帮助下自己动手完成了任务</option>
            <option value="在父母帮助下完成了任务">在父母帮助下完成了任务</option>
            <option value="在家人的帮助下完成了任务">在家人的帮助下完成了任务</option>
          </select>
          <input placeholder='任务用时' style={{width: '5em'}} type='number' name='time' value={this.state.time} onChange={e=>this.handleInputChange(e)}/>
        </div>
        {/** 创新能力 */}
        <div>
          <h3>创新能力</h3>
          <input type='checkbox' name='by_video' checked={this.state.by_video} onChange={e=>this.handleInputChange(e)} id={'by_video'+this.props.spawnerID}/>
          <label htmlFor={'by_video'+this.props.spawnerID}>只按照视频中的内容完成了任务</label>
          <br/>
          <input placeholder='补充信息: 孩子创新的地方' size='48' type='text' name='innovation' value={this.state.innovation} onChange={e=>this.handleInputChange(e)}/>
        </div>
        {/** 交流表达能力 */}
        <div>
          <h3>交流表达能力</h3>
          <select name='c_method' value={this.state.c_method} onChange={e=>this.handleInputChange(e)}>
            <option value="可以语音进行交流">可以语音进行交流</option>
            <option value="使用文字进行交流">使用文字进行交流</option>
          </select>
          {
              this.state.c_method === '可以语音进行交流' ? (
                  <select name='c_info' value={this.state.c_info} onChange={e=>this.handleInputChange(e)}>
                    <option value="并且交流表达较为清晰">并且交流表达较为清晰</option>
                    <option value="但部分时间麦克风有电流声音">但部分时间麦克风有电流声音</option>
                    <option value="麦克风电流声音较大, 难以听清">麦克风电流声音较大, 难以听清</option>
                    <option value="杂音较大, 很难听清">杂音较大, 很难听清</option>
                  </select>
                ) : (
                  <select name='c_info' value={this.state.c_info} onChange={e=>this.handleInputChange(e)}>
                    <option value="打字速度中等">打字速度中等</option>
                    <option value="打字速度较慢">打字速度较慢</option>
                    <option value="打字速度较快">打字速度较快</option>
                  </select>
                )
          }
          <br/>
          <input type='checkbox' name='can_ask' checked={this.state.can_ask} onChange={e=>this.handleInputChange(e)} id={'can_ask'+this.props.spawnerID}/>
          <label htmlFor={'can_ask'+this.props.spawnerID}>可以主动向老师提出问题</label>
          <br/>
          <input placeholder='补充信息: 孩子交流方面的其他问题' size='48' type='text' name='communication' value={this.state.communication} onChange={e=>this.handleInputChange(e)}/>
        </div>
        {/** 设备信息 */}
        <div>
          <h3>其他信息</h3>
          <input type='checkbox' name='network' checked={this.state.network} onChange={e=>this.handleInputChange(e)} id={'network'+this.props.spawnerID}/>
          <label htmlFor={'network'+this.props.spawnerID}>网络不太稳定</label>
          <input type='checkbox' name='computer' checked={this.state.computer} onChange={e=>this.handleInputChange(e)} id={'computer'+this.props.spawnerID}/>
          <label htmlFor={'computer'+this.props.spawnerID}>电脑操作能力较弱</label>
          <br/>
          <input placeholder='补充信息: 其他信息' size='48' type='text' name='else_info' value={this.state.else_info} onChange={e=>this.handleInputChange(e)}/>
        </div>
        {/** 生成反馈 */}
        <div className='button' onClick={()=>this.spawn()}>
          {this.state.show_result ? '隐藏反馈' : '生成反馈'}
        </div>
        {
          this.state.show_result ? (
            <div>
              {this.state.result.split("\n").map(i => { return <div key={i}>{i}</div>; })}
              <div className='button' style={{marginBottom: '20px'}} onClick={()=>{ copy(this.state.result); alert('复制成功')}}>复制</div>
            </div>
          ) : (
            <div></div>
          )
        }
      </div>
    );
  }
}

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      spawner: []
    }
    this.onUnload = this.onUnload.bind(this);
  }

  onUnload = e => {
    e.returnValue = "刷新后已填写模板将被清除";
    return "刷新后已填写模板将被清除";
  }

  componentDidMount() {
    window.addEventListener("beforeunload", this.onUnload);
  }

  componentWillUnmount() {
      window.removeEventListener("beforeunload", this.onUnload);
  }

  addSpawner() {
    const arr = [...this.state.spawner];
    arr.push(<Spawner className='container' spawnerID={arr.length}></Spawner>);
    this.setState({
      spawner: arr
    });
  }
  render() {
    return (
      <div>
        <div className='button' onClick={()=>this.addSpawner()}>新建反馈模板</div>
        <div className='main'>
          {this.state.spawner}
        </div>
      </div>
      
    );
  }
}